import { MaskPipe } from 'ngx-mask';

import { CurrencyPipe } from '@angular/common';
import { Component, Injector, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CoreException } from '@nequi/commons/exceptions';
import { ModuleConfigProvider, PhoneNumberConfigProvider } from '@nequi/features/commons';
import { Transaction } from '@nequi/features/commons/core';
import { TransactionBasePage } from '@nequi/features/commons/presentation';
import { ContactsInteractor } from '@nequi/features/contacts/core';
import { Pocket } from '@nequi/features/money-management/core';
import {
  MaxAllowedTransactionsException,
  TransactionType,
  TransfiYaInteractor,
  TransfiYaSendTransaction
} from '@nequi/features/transfi-ya/core';
import { BottomSheetController, CardModalService, ConfirmInfoPocketProps } from '@nequi/ui';
import { openUrl, toCamelCaseProperties } from '@nequi/utils/functions';
import { TranslatePipe } from '@ngx-translate/core';

import {
  SendNequiConfirmationOrganism
} from '../../organisms/send-nequi-confirmation/send-nequi-confirmation.organism';
import { SendViewModel } from '../send/send.view-model';
import { SendConfig } from './config';

type SendPageState = {
  pocket: Pocket;
};

type ConfirmationMessages = {
  title: string;
  content: string;
  image: string;
  buttons: {
    nequi: string;
    transfiYa: string;
  };
};

@Component({
  selector: 'app-transfi-ya-send',
  templateUrl: './send.page.html',
  styleUrls: ['./send.page.scss'],
})
export class SendPage extends TransactionBasePage implements OnInit {
  public balanceToogle = false;

  constructor(
    public viewModel: SendViewModel,
    private interactor: TransfiYaInteractor,
    private router: Router,
    private contactsInteractor: ContactsInteractor,
    private bottomSheetController: BottomSheetController,
    private currencyPipe: CurrencyPipe,
    private translatePipe: TranslatePipe,
    private maskPipe: MaskPipe,
    private phoneNumberConfigProvider: PhoneNumberConfigProvider,
    private route: ActivatedRoute,
    private cardModal: CardModalService,
    private moduleConfigProvider: ModuleConfigProvider,
    injector: Injector
  ) {
    super(injector);
  }

  public async ngOnInit(): Promise<void> {
    this.translateProvider.loadModule(this.moduleConfigProvider.i18n.moduleKey);
    const sendInfo = this.router.getCurrentNavigation().extras.state as SendPageState;

    this.viewModel.setPocket(sendInfo.pocket);
    const limits = await this.interactor.getLimits();
    const fee = await this.interactor.getFee();

    this.viewModel.setLimits(limits);
    this.viewModel.setFee(fee);
  }

  public async confirmTransaction() {
    try {
      this.viewModel.isLoading = true;

      const isNequi = await this.contactsInteractor.isNequiUser(this.viewModel.contact.value);

      this.viewModel.isLoading = false;

      if (!isNequi) {
        return this.makeTransaction();
      }

      await this.showSendNequiConfirmation();
    } catch (error) {
      this.toastService.showError(error.message);
      this.viewModel.isLoading = false;
    }
  }

  protected async setOriginMoney(transaction: Transaction) {
    if (!transaction?.pocket || transaction?.pocket === null) {
      const pocket = this.originMoneyService.getAvailablePocket();

      transaction.withPocket(pocket);
    }

    return transaction;
  }

  protected async getConfirmationInfo(
    transaction: TransfiYaSendTransaction
  ): Promise<ConfirmInfoPocketProps> {
    const messages = this.translateProvider.get(SendConfig.i18n.confirmation);

    return {
      title: messages.title,
      items: [
        {
          label: messages.cel,
          value: transaction.data.contact.phoneNumber,
        },
        {
          label: messages.amount,
          value: this.currencyPipe.transform(transaction.amount),
        },
      ],
    };
  }

  protected async onTransactionSuccess(_transaction: TransfiYaSendTransaction): Promise<void> {
    const helpUrl = await this.interactor.getHelpUrl();
    const messages = this.translateProvider.get(SendConfig.i18n.success);

    this.cardModal.show({
      imgPath: messages.image,
      title: messages.title,
      content: this.translateProvider.get(SendConfig.i18n.successContent, {
        timeLimit: this.viewModel.limits.maxHoursToAcceptTransaction,
      }),
      buttons: [
        {
          text: messages.button,
          color: SendConfig.styles.confirmation.button.color,
          handler: () => this.finishTransaction(),
        },
        {
          text: messages.help,
          isLink: true,
          handler: () => {
            openUrl(helpUrl);
            this.finishTransaction();
          },
        },
      ],
      nativeBackButtonAction: () => this.finishTransaction(),
    });
  }

  protected async onTransactionFailed(error: CoreException) {
    if (error.constructor === MaxAllowedTransactionsException) {
      this.alertService.showByI18n({ i18nKey: SendConfig.i18n.maxAllowedTransactionsError });

      return;
    }

    throw error;
  }

  private makeTransaction() {
    const transaction = new TransfiYaSendTransaction()
      .withAmount(this.viewModel.amount.value)
      .withData({
        contact: this.viewModel.contact.value,
        message: this.viewModel.message.value,
        transactionType: TransactionType.SEND,
      })
      .withPocket(this.viewModel.form.value.pocket);

    super.executeTransaction(transaction);
  }

  private async showSendNequiConfirmation() {
    const messages = toCamelCaseProperties<ConfirmationMessages>(
      this.translateProvider.get(SendConfig.i18n.sendNequiConfirmation)
    );

    const contact = this.viewModel.contact.value?.completeName
      ? this.viewModel.contact.value?.completeName
      : this.maskPipe.transform(
          this.viewModel.contact.value.phoneNumber,
          this.phoneNumberConfigProvider.mask
        );

    const bottomSheet = await this.bottomSheetController.create({
      component: SendNequiConfirmationOrganism,
      componentProps: {
        title: messages.title,
        image: messages.image,
        content: this.translatePipe.transform(SendConfig.i18n.sendNequiConfirmationContent, {
          phoneNumber: contact,
        }),

        actions: [
          {
            label: messages.buttons.nequi,
            color: SendConfig.styles.sendNequiConfirmation.nequiButton.color,
            fill: SendConfig.styles.sendNequiConfirmation.nequiButton.fill,
            action: () => {
              bottomSheet.close();
              this.navigatorProvider.navigateForward([SendConfig.routes.sendNequi], {
                relativeTo: this.route,
                state: {
                  contact: this.viewModel.contact.value,
                  message: this.viewModel.message.value,
                  amount: this.viewModel.amount.value,
                  pocket: this.viewModel.pocketControl.value,
                  initTransactionOnLoad: true,
                },
              });
            },
          },
          {
            label: messages.buttons.transfiYa,
            color: SendConfig.styles.sendNequiConfirmation.transfiYa.color,
            fill: SendConfig.styles.sendNequiConfirmation.nequiButton.fill,
            action: () => {
              const time = 400;

              bottomSheet.close();
              setTimeout(() => {
                this.makeTransaction();
              }, time);
            },
          },
        ],
      },
      componentEvents: { closed: () => bottomSheet.close() },
      isAutoHeight: true,
      hasBackdrop: true,
      isBackdropDismiss: true,
      isBackButtonDismiss: true,
    });

    bottomSheet.open();
  }

  private finishTransaction() {
    const messages = this.translateProvider.get(SendConfig.i18n.success);

    this.navigatorProvider.navigateRoot([SendConfig.routes.end]).then(() => {
      this.toastService.showSuccess(messages.toast);
    });
  }
}
