import { Injectable } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Pocket } from '@nequi/features/money-management/core';
import { LimitsEntity } from '@nequi/features/transfi-ya/core';

import { SendConfig } from './config';

@Injectable()
export class SendViewModel {
  public form: FormGroup;
  public contact: FormControl;
  public amount: FormControl;
  public message: FormControl;
  public pocketControl: FormControl;
  public isLoading: boolean;

  public limits: LimitsEntity;
  public fee: number;

  constructor() {
    this.createFormGroup();
  }

  public get maxAmount(): number {
    return this.limits?.maxAmount;
  }

  public get currencyFormat(): string {
    return SendConfig.currencyFormat;
  }

  public setPocket(pocket: Pocket) {
    this.pocketControl.setValue(pocket);
  }

  public setLimits(limits: LimitsEntity) {
    this.limits = limits;
    this.amount.setValidators([
      Validators.required,
      Validators.min(SendConfig.validations.amount.min),
      Validators.max(limits.maxAmount),
    ]);
  }

  public setFee(fee: number) {
    this.fee = fee;
  }

  private createFormGroup() {
    this.crateControls();
    this.form = new FormGroup({
      pocket: this.pocketControl,
      contact: this.contact,
      amount: this.amount,
      message: this.message,
    });

    this.form.markAsPristine();
    this.form.markAsUntouched();
  }

  private crateControls() {
    this.contact = new FormControl(null, [Validators.required]);
    this.amount = new FormControl(null, [Validators.required]);

    this.message = new FormControl(null, [
      Validators.maxLength(SendConfig.validations.message.maxLength),
      Validators.pattern(SendConfig.validations.message.pattern),
    ]);

    this.pocketControl = new FormControl();
  }
}
